import React, { Component } from 'react';
import { Table, Button, Spin, Icon, Tag } from 'antd';
import { connect } from 'react-redux';
import * as creators from './store/creators';
import Oper from './components/Operation';
import Add from './components/Add';
import Edit from './components/Edit';
import styles from './css/UserList.module.css';

const columns = [
  {
    title: '',
    dataIndex: 'menuName',
    key: 'menuName',
  },
  {
    title: '',
    dataIndex: 'menuRoute',
    key: 'menuRoute',
  },
  {
    title: '',
    dataIndex: 'menuLogo',
    key: 'menuLogo',
    align: 'center',
    render: menuLogo => (
      <Icon type={menuLogo} />
    )
  },
  {
    title: '',
    dataIndex: 'menuStatue',
    key: 'menuStatue',
    align: 'center',
    render: roleStatue => (
      <span>
        {
          roleStatue === 1 ?
            <Tag color="green"></Tag> :
            <Tag color="red"></Tag>
        }
      </span>
    ),
  },

  {
    title: '',
    dataIndex: 'operation',
    key: 'operation',
    render: (text, record) => <Oper text={text} record={record} />,
  },
];

class MenuList extends Component {
  componentDidMount() {
    this.props.queryMenuList({ props: this.props, data: {} });
  }

  render() {
    const list = this.props.list

    return (
      <div className={`${styles.pageContet} pageContentColor`}>

        <Spin tip="Loading..." spinning={this.props.spinning}>
          <div className={styles.buttonForm}>
            <Button
              type="primary"
              className={styles.addButton}
              onClick={() => this.props.changeAddModalvisible(true)}
            ><Icon type="plus" /></Button>
          </div>
          <Table
            bordered
            expandIconAsCell={true}
            columns={columns}
            dataSource={list}
            rowKey={record => record.key}
            size="small"
            pagination={false}
          />
        </Spin>
        <Add />
        <Edit />
      </div >
    )
  }
}

const mapState = state => ({
  list: state.menu.list,
  spinning: state.menu.spinning,
  addModalvisible: state.menu.addModalvisible,
})

const mapDispatch = dispatch => ({
  queryMenuList: req => {
    const action = creators.queryMenuAction(req);
    dispatch(action);
  },
  changeAddModalvisible: visible => {
    const action = creators.changeAddModalvisibleAction(visible);
    dispatch(action);
  }
})
export default connect(mapState, mapDispatch)(MenuList);